import React from "react";
import "./product.css";
import SkateMultipackBox from "../SkateMultipackBoxWithCookies.png";

class Product extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    return (
    <div className="product" id="OurProduct">
      <div className="twoRowLeft">
        <div className="textField">
          <p>Gezonde kinderkoeken<br/>met een buitenaardse balans<br/>tussen voedzaam en lekker</p>
          <div className="yellowBar1"></div>
          <div className="yellowBar2"></div>
          <div className="yellowBar3"></div>
        </div>
      </div>
      <div className="twoRowRight">
        <img src={SkateMultipackBox}></img>
      </div>
    </div>);
  }
}

export default Product;
