import React from "react";
import "./story.css";
import PieceMonkey from "../PieceMonkey.png";

class Story extends React.Component {
    constructor(props) {
      super(props);
      this.state = {};
    }
  
    render() {
      return (
        <div className="story" id="OurStory">
          <div className="storyImageContainer">
            <img src={PieceMonkey}></img>
          </div>
          <div className="storyTextContainer">
            <div>
            <p>Jarenlang zweefde Kiko door de ruimte op zoek naar de meest lekkere en gezonde ingrediënten om koekjes te maken… Tot hij op een dag op onze Aarde botste en wild werd van al die heerlijke grondstoffen die onze planeet te bieden heeft. Kiko ging meteen aan de slag en maakte hét koekje dat jou de energie geeft om je zware dag door te komen. Apetrots is hij! Proef je mee van Kiko’s knapperige cookies en luister je mee naar zijn knotsgekke ruimteverhalen?</p>
            <p>Kiko’s gezonde koekje is dé snack die jouw kind de juiste elementen geeft om doorheen haar of zijn zware dagtaak te komen. Want wat is een gezond koekje? Die vraag houdt ons al lange tijd bezig. Biologisch? Laag aan calorieën? Daarom ontwikkelde de overheid de Nutriscore, die op basis van heel wat verschillende factoren aangeeft wat de voedingswaarde van een product is. Wij gingen samen met een diëtist en nutrioneel expert aan de slag en gingen voor niet minder dan de hoogste score. Want alleen het beste is goed genoeg voor jouw kind.</p>
            <p>Kortom… dit koekje is simpelweg gezond en geeft jouw kind voldoende energie en een verzadigd gevoel zonder in te boeten op smaak.</p>
            <a href="mailto:info@kikofood.com"><button>CONTACTEER ONS</button></a>
            </div>
          </div>
        </div>
      );
    }
}

export default Story;